// -------  -------
// #region
export const initLocalData = () => {
    if (getItem('isInit')) return false;

    // 
    setItem('regular', regular);

    // 
    setItem('theme', theme);

    // ToDo
    setItem('fixedToDoGroup', fixedToDoGroup);

    // todo
    setItem('toDoList', toDoList);

    // 
    setItem('backgroundImg', backgroundImg)

    // 
    setItem('isInit', true)
}
// #endregion
// ------- end -------